const EMAIL_REGX = /^[_a-z0-9-+]+(\.[_a-z0-9-+]+)*(\+[a-z0-9-]+)?\u0040[a-z0-9-.]+(\.[a-z0-9]+)$/;
const PAN_REGX = /([A-Z]){5}([0-9]){4}([A-Z]){1}$/;

const emailValidator = (email) => {
  email = email.toLowerCase();
  if (EMAIL_REGX.test(email) === false) {
    return { isValid: false, email }; // In-Valid Email
  }

  return { isValid: true, email }; // Valid Email
};

const panValidator = (pan) => {
  pan = pan.toUpperCase();
  if (PAN_REGX.test(pan) === false) {
    return { isValid: false, pan }; // In-Valid PAN
  }

  return { isValid: true, pan }; // Valid PAN
};

export { emailValidator, panValidator };
