import 'bootstrap/dist/css/bootstrap.min.css';
import React from 'react';
import { Button, Spinner } from 'react-bootstrap';
import { FiEye, FiEyeOff } from 'react-icons/fi';
import { toast } from 'react-toastify';
import db from '../../../../../libs/db';
import AdminServices from '../../../../../services/api/AdminServices';
import AuthHeader from '../../../components/molecules/Headers/AuthHeader';

export default function EnterPassScreen(props) {

	const [password, setPassword] = React.useState('');
	const [nextbtnPressed, setnextbtnPressed] = React.useState(false);
	const [passstate, setpassstate] = React.useState(false);
	const [helperType, sethelperType] = React.useState('');
	const [helperColor, sethelperColor] = React.useState('');
	const [helperText, sethelperText] = React.useState('');
	const [loading, setloading] = React.useState(true);

	const setPasswordVal = (text) => {
		sethelperType('');
		sethelperColor('');
		sethelperText('');
		setPassword(text);
	};


	function showPass() {
		var temp = document.getElementById("typepass");
		if (temp.type === "password") {
			temp.type = "text";
			setpassstate(!passstate);
		}
		else {
			temp.type = "password";
			setpassstate(!passstate);
		}
	}

	const onNextBtnPressed = async () => {
		setnextbtnPressed(true);

		const data = {
			email: props.location.state.email,
			password,
			pin: '',
			isIFA: false
		};

		const a = ['a,', 'a'];

		try {
			const res = await AdminServices.loginwithPass(data).then((r) => r);
			if (res.type) {
				if (res.data.Result === 'Login Successful.') {
					const x = props.location.state.ClientName;
					const nameparts = x.split(' ');
					const initials = nameparts.length > 1
						? nameparts[0].charAt(0).toUpperCase()
						+ nameparts[1].charAt(0).toUpperCase()
						: nameparts[0].charAt(0).toUpperCase(); // Output: SR
					const userData = {
						name: props.location.state.ClientName,
						email: props.location.state.email,
						id: props.location.state.ClientId,
						isLogged: true,
						profileComplete: res.data.IsProfileComplete !== 0,
						initials,
						screenType: 1,
						users: [props.location.state.ClientName]
					};

					db.set(db.model.user, JSON.stringify({ userData }));
					props.history.push("/pro")

				} else {
					setnextbtnPressed(false);
					console.log('ERR : 2', res.msg);
					toast.error("Try again ...");

				}
			} else {
				setnextbtnPressed(false);
				console.log('ERR : 1', res.msg);
				sethelperType('pass');
				sethelperColor('red');
				sethelperText('Incorrect password. Try again!');
				toast.error("Try again ...");

			}
		} catch (error) {
			setnextbtnPressed(false);
			console.log('ERR : c', error);
			toast.error("Try again ...");

		}
	};


	React.useEffect(() => {
		const { state } = props.location
		console.log(state)
		if (state === null || state === undefined) {
			props.history.push('/email')
		}
		setloading(false)
	})


	if (loading) {
		return (
			<div className="container">
				<Spinner
					as="span"
					animation="grow"
					size="sm"
					role="status"
					aria-hidden="true"
				/>
			</div>
		);
	} else {

		return <div className="container">
			<AuthHeader navigation={props.history} />
			<h3 className="textHeadauth">Welcome back, {props.location.state.ClientName}!</h3>
			<div className="floating-form">
				<div className="floating-label" >
					<input onChange={(p) => setPasswordVal(p.target.value)} className="floating-input" id="typepass" type="password" placeholder="" />
					<span className="highlight"></span>
					<label className="floating-label-input" >Password</label>
					{passstate ? (<FiEye className="i" onClick={() => showPass()} />) : (<FiEyeOff className="i" onClick={() => showPass()} />)}
				</div>
				<div style={{ display: 'flex', justifyContent: "space-between" }}>

					<span className="textHelper primary" style={{ color: helperColor }} >{helperText}</span>
					<span className="textNormal primary" onClick={() => {
						props.history.push({
							pathname: '/reset_pass',
							state: { ...props.location.state } // your data array of objects
						})
					}} >Forgot Password?</span>
				</div>
			</div>
			<div className="footer">
				{nextbtnPressed ? (
					<>
						<Button variant="outline-light" style={{
							color: '#fff',
							backgroundColor: '#00A6A4',
							opacity: 0.4,
							borderRadius: 10,
							width: '100%',
							height: '44px'
						}}>
							<Spinner
								as="span"
								animation="grow"
								size="sm"
								role="status"
								aria-hidden="true"
							/>
							Sign in...
						</Button>

					</>
				) : (
					<Button variant="outline-light" style={{
						color: '#fff',
						backgroundColor: '#00A6A4',
						borderRadius: 10,
						width: '100%',
						height: '44px',
						opacity: password.length >= 8 ? 1 : 0.4,

					}}
						onClick={() => onNextBtnPressed()}
					>
						Sign in
					</Button>)}
			</div>
		</div>
	}
}
