import React from 'react';
import colorsFinal from '../../../../theme/colors';

export default function CountDown({ style, totalTime, onPress, dir }) {
    // const { dir = 'down', style = {}, onPress = () => {}, time } = props;
    let time = totalTime;
    const [sec, setSec] = React.useState('00');
    const [min, setMin] = React.useState('00');

    const timify = (t) => {
        t /= 1000;
        if (t >= 0) {
            if (t > 60) {
                t = '00';
            }
            if (t < 9) {
                t = `0${t}`;
            }
            return t;
        }
        return '00';
    };

    React.useEffect(() => {
        timer();
    }, []);

    const timer = () => {
        setInterval(() => {
            if (time >= 0) {
                if (dir === 'down') {
                    time -= 1000;
                }
                if (dir === 'up') {
                    time += 1000;
                }
                setSec(timify(time));
            }
        }, 1000);
    };

    const resetTimer = () => {
        setInterval(() => {
            if (time >= 0) {
                if (dir === 'down') {
                    time -= 1000;
                }
                if (dir === 'up') {
                    time += 1000;
                }
                setSec(timify(time));
            }
        }, 1000);
    };

    if (`${min}:${sec}` !== '00:00') {
        return (
            <div style={{ display: "flex", flexDirection: "row", justifyContent: 'center', margin: "2%,5%" }}>
                <p>{`${min}:${sec}`}</p>
            </div>
        );
    }
    return (
        <div style={{ paddingVertical: 20 }}>
            <p className="textNormal" style={{ marginTop: '10px', textAlign: "left" }}>
                Didn’t receive OTP?{' '}
                <span
                    onClick={() => {
                        alert("hey")
                        resetTimer();
                        onPress();
                    }}
                    style={{ color: colorsFinal.brandPrimary }}>
                    Request again
                </span>
            </p>
        </div>
    );
}
