import React from 'react';
import { Spinner } from 'react-bootstrap';
import { toast } from 'react-toastify';
import AdminServices from '../../../../../services/api/AdminServices';
import CountDown from '../../../components/atoms/CountDown';
import AuthHeader from '../../../components/molecules/Headers/AuthHeader';

export default function OTPScreen(props) {

	const [otp, setOtp] = React.useState('');
	const [validOTP, setValidOTP] = React.useState(false);
	const [invalidOTP, setinvalidOTP] = React.useState(false);
	const [loading, setloading] = React.useState(true);
	const [mainOTP, setmainOTP] = React.useState('');

	const resendOTP = async () => {
		try {
			const res = await AdminServices.sendOTP(
				props.location.state.phone,
				props.location.state.email
			).then((res) => res);
			console.log(res.data);
			if (res.type) {
				if (res.data.Result === 'success') {
					setmainOTP(res.data.otpcode);
				} else {
					toast.error("Try again ...");
				}
			} else {
				toast.error("Try again ...");
			}
		} catch (error) {
			toast.error("Try again ...");
		}
	};

	const checkOTP = (otp) => {
		try {
			if (otp === `${mainOTP}`) {
				setValidOTP(true);
				setinvalidOTP(false);
			} else {
				setValidOTP(false);
				setinvalidOTP(true);
			}
		} catch (error) {
			console.log('hey1', error);
		}
	};

	React.useEffect(() => {
		if (validOTP) {
			setTimeout(() => {
				props.history.push({
					pathname: '/setup_pass',
					state: { ...props.location.state } // your data array of objects
				})
			}, 1000);
		}
	}, [validOTP]);



	React.useEffect(() => {
		const { state } = props.location
		console.log(state)
		if (state === null || state === undefined) {
			props.history.push('/email')
		} else {
			setValidOTP(false);
			setmainOTP(state.otp);
			setloading(false)
		}
	}, [])


	if (loading) {
		return (
			<div className="container">
				<Spinner
					as="span"
					animation="grow"
					size="sm"
					role="status"
					aria-hidden="true"
				/>
			</div>
		);
	} else {
		return <div className="container">
			<AuthHeader navigation={props.history} />
			<h3 className="textHeadauth">Authentication via OTP</h3>
			<p className="textNormal place-holder" >Enter OTP received on {props.location.state.email} and {props.location.state.phone}
				<span onClick={() => {
					props.history.goBack();
				}} className="primary"> Change email/phone</span></p>
			<center>
				<div className="floating-form">
					<div className="floating-label">
						<input className="floating-input" value={otp} onChange={(z) => {
							setOtp(z.target.value);
							if (z.target.value.length === 6) {
								checkOTP(z.target.value)
							}

						}} maxLength={6} type="number" placeholder="" style={{ width: '25%', textAlign: 'center' }} />
						<span className="highlight"></span>
					</div>
				</div>

				<CountDown totalTime={30000} style={{ marginTop: 24 }} dir="down" onPress={() => { resendOTP(); }} />

				{invalidOTP ? (
					<p className="textNormal" style={{ color: "red" }}>
						Incorrect OTP. Try again!
					</p>
				) : (true)}
			</center>
		</div>
	}
}
