import React from "react";
import AuthHeader from "../../../components/molecules/Headers/AuthHeader";
// import "bootstrap/dist/css/bootstrap.min.css";

export default function DashScreen(props) {
  React.useEffect(() => {
  }, []);

  const changeThemeVal = () => {
    var theme = window.localStorage.getItem("theme")
    if (theme === null || theme === undefined) {
      window.localStorage.setItem("theme", 'dark')
      document.documentElement.setAttribute("data-theme", "dark")
      document.body.style = 'background: #001516;';
    } else {
      if (theme === 'dark') {
        window.localStorage.setItem("theme", 'light')
        document.documentElement.setAttribute("data-theme", "light")
        document.body.style = 'background: #fff;';
      } else {
        window.localStorage.setItem("theme", 'dark')
        document.documentElement.setAttribute("data-theme", "dark")
        document.body.style = 'background: #001516;';
      }
    }
  }

  return (
    <div className="container">
      <AuthHeader screenType="Home" navigation={props.history} />
      <h1>DashScreen</h1>
      <p onClick={() => {
        localStorage.clear("isAuthenticated")
        props.history.push("/email")
      }}>Logout</p>
      <p onClick={() => changeThemeVal()}>Change Theme</p>
    </div>

  );
}
