import React, { useState } from 'react';
import { Button, Spinner } from 'react-bootstrap';
import { browserName, deviceType, mobileModel, osName, osVersion } from 'react-device-detect';
import { FiEye, FiEyeOff } from 'react-icons/fi';
import { toast } from 'react-toastify';
import db from '../../../../../libs/db';
import AdminServices from '../../../../../services/api/AdminServices';
import AuthHeader from '../../../components/molecules/Headers/AuthHeader';

export default function SetupPasswordScreen(props) {

	const [password, setPassword] = React.useState('');
	const [showSuccess, setShowSuccess] = React.useState(false);
	const [confirmed, setConfirmed] = React.useState(false);
	const [nextbtnPressed, setnextbtnPressed] = React.useState(false);
	const [helperType, sethelperType] = React.useState('');
	const [helperColor, sethelperColor] = React.useState('');
	const [helperText, sethelperText] = React.useState('');
	const [loading, setloading] = React.useState(true);

	const isValid = () => {
		if (password !== '' && password.length >= 8 && helperColor !== 'red') {
			return true;
		}
		return false;
	};


	const checkPass = (pass) => {
		setPassword(pass);
		if (
			pass.match(
				/^(?=.*[0-9])(?=.*[!@#$%^&*])(?=.*[a-z])(?=.*[A-Z])[a-zA-Z0-9!@#$%^&*]{8,80}$/
			)
		) {
			sethelperType('pass');
			sethelperColor('green');
			sethelperText('Excellent');
		} else if (
			pass.match(/^(?=.*[0-9])(?=.*[a-z])[a-zA-Z0-9!@#$%^&*]{8,80}$/)
		) {
			sethelperType('pass');
			sethelperColor('orange');
			sethelperText('Good');
		} else {
			sethelperType('pass');
			sethelperColor('red');
			sethelperText('Weak');
		}
	};

	const onContinuePressed = async () => {
		setnextbtnPressed(true);
		const data = {
			clientId: 0,
			email: props.location.state.email,
			mobileNumber: props.location.state.phone,
			clientName: props.location.state.name,
			password,
			source: deviceType + ":" + browserName + ":" + (mobileModel === "none" ? osName : mobileModel) + ":" + osVersion
		};
		try {
			const res = await AdminServices.createUser(data).then((res) => res);
			if (res.type) {
				if (res.data.Result === 'success') {
					try {
						const x = props.location.state.name;
						const nameparts = x.split(' ');
						const initials = nameparts.size > 1
							? nameparts[0].charAt(0).toUpperCase()
							+ nameparts[1].charAt(0).toUpperCase()
							: nameparts[0].charAt(0).toUpperCase(); // Output: SR
						const userData = {
							name: props.location.state.name,
							email: props.location.state.email,
							phone: props.location.state.phone,
							id: res.data.ClientId,
							isLogged: true,
							profileComplete: false,
							initials,
							screenType: 1,
							users: [props.location.state.name]
						};
						db.set(db.model.user, JSON.stringify({ userData }));
						props.history.push("/pro")
					} catch (error) {
						setnextbtnPressed(false);
						toast.error("Try again ...");
					}
				} else {
					setnextbtnPressed(false);
					toast.error("Try again ...");
				}
			} else {
				setnextbtnPressed(false);
				toast.error("Try again ...");
			}
		} catch (error) {
			setnextbtnPressed(false);
			toast.error("Try again ...");
		}
	};

	const [pass, setpass] = useState(false);

	function showPass() {
		var temp = document.getElementById("typepass");
		if (temp.type === "password") {
			temp.type = "text";
			setpass(!pass);
		}
		else {
			temp.type = "password";
			setpass(!pass);
		}
	}

	React.useEffect(() => {
		const { state } = props.location
		console.log(state)
		if (state === null || state === undefined) {
			props.history.push('/email')
		} else {
			setloading(false)
		}
	}, [])


	if (loading) {
		return (
			<div className="container">
				<Spinner
					as="span"
					animation="grow"
					size="sm"
					role="status"
					aria-hidden="true"
				/>
			</div>
		);
	} else {
		return <div className="container">
			<AuthHeader naviagtion={props.history} />
			<h3 className="textHeadauth">Set password</h3>
			<div class="floating-form">
				<div class="floating-label" >
					<input class="floating-input" value={password} onChange={(p) => checkPass(p.target.value)}
						id="typepass" type="password" placeholder="" />
					<span class="highlight"></span>
					<label className="floating-label-input" >Password</label>
					{pass ? (<FiEye class="i" onClick={() => showPass()} />) : (<FiEyeOff class="i" onClick={() => showPass()} />)}
				</div>
				{helperType === 'pass' ? (
					<p className="textHelper" style={{ marginTop: '10px', }}>
						Password strength:{' '}
						<span className="textHelper" style={{ color: helperColor }}>
							{helperText}
						</span>
					</p>
				) : null}
			</div>
			<div className="footer">
				{nextbtnPressed ? (
					<>
						<Button variant="outline-light" style={{
							color: '#fff',
							backgroundColor: '#00A6A4',
							opacity: 0.4,
							borderRadius: 10,
							width: '100%',
							height: '44px'
						}}>
							<Spinner
								as="span"
								animation="grow"
								size="sm"
								role="status"
								aria-hidden="true"
							/>
							Confirm
						</Button>

					</>
				) : (
					<Button variant="outline-light" style={{
						color: '#fff',
						backgroundColor: '#00A6A4',
						borderRadius: 10,
						width: '100%',
						height: '44px',
						opacity: isValid() ? 1 : 0.4,
					}}
						onClick={() => onContinuePressed()}
					>
						Confirm
					</Button>)}
			</div>
		</div>
	}
}
