import React, { useState } from 'react';
import { Button, Modal, Spinner } from 'react-bootstrap';
import { FiEye, FiEyeOff } from 'react-icons/fi';
import { toast } from "react-toastify";
import AdminServices from '../../../../../services/api/AdminServices';
import colorsFinal from '../../../../../theme/colors';
import AuthHeader from '../../../components/molecules/Headers/AuthHeader';

export default function SetupPasswordScreen(props) {

    const [password, setPassword] = React.useState('');
    const [showSuccess, setShowSuccess] = React.useState(false);
    const [confirmed, setConfirmed] = React.useState(false);
    const [nextbtnPressed, setnextbtnPressed] = React.useState(false);
    const [helperType, sethelperType] = React.useState('');
    const [helperColor, sethelperColor] = React.useState('');
    const [helperText, sethelperText] = React.useState('');
    const [loading, setloading] = React.useState(true);
    const [dshow, setdshow] = React.useState(false);
    const [ddata, setddata] = React.useState([]);

    const isValid = () => {
        if (password !== '' && password.length >= 8 && helperColor !== 'red') {
            return true;
        }
        return false;
    };

    const checkPass = (pass) => {
        setPassword(pass);
        if (
            pass.match(
                /^(?=.*[0-9])(?=.*[!@#$%^&*])(?=.*[a-z])(?=.*[A-Z])[a-zA-Z0-9!@#$%^&*]{8,80}$/
            )
        ) {
            sethelperType('pass');
            sethelperColor('green');
            sethelperText('Excellent');
        } else if (
            pass.match(/^(?=.*[0-9])(?=.*[a-z])[a-zA-Z0-9!@#$%^&*]{8,80}$/)
        ) {
            sethelperType('pass');
            sethelperColor('orange');
            sethelperText('Good');
        } else {
            sethelperType('pass');
            sethelperColor('red');
            sethelperText('Weak');
        }
    };

    const onContinuePressed = async () => {
        setnextbtnPressed(true);
        var data = {
            email: props.match.params.email,
            password: password
        };
        try {
            var res = await AdminServices.savePass(data).then((res) => {
                return res;
            });
            console.log(res);
            if (res.type) {
                if (res.data.Result === 'success') {
                    try {
                        toast.success("Password changed successfully");
                        props.history.push("/email")
                    } catch (error) {
                        setnextbtnPressed(false);
                        toast.error("Try again ...");
                    }
                } else {
                    setnextbtnPressed(false);
                    toast.error("Try again ...");
                }
            } else {
                setnextbtnPressed(false);
                toast.error("Try again ...");
            }
        } catch (error) {
            setnextbtnPressed(false);
            toast.error("Try again ...");
        }
    };

    const [pass, setpass] = useState(false);

    function showPass() {
        var temp = document.getElementById("typepass");
        if (temp.type === "password") {
            temp.type = "text";
            setpass(!pass);
        }
        else {
            temp.type = "password";
            setpass(!pass);
        }
    }

    const validateToken = async () => {
        var data = {
            email: props.match.params.email,
            resetCode: props.match.params.token,
        }
        try {
            const res = await AdminServices.validateToken(data).then((res) => res);
            console.log(res);
            if (res.type) {
                if (res.data.Result === 'success') {
                    try {
                        setloading(false)
                    } catch (error) {
                        setnextbtnPressed(false);
                        props.history.push('/email')
                        toast.error("Try again ...");
                    }
                } else {
                    setnextbtnPressed(false);
                    props.history.push('/email')
                    toast.error("Try again ...");
                }
            } else {
                setnextbtnPressed(false);
                props.history.push('/email')
                toast.error("Try again ...");
            }
        } catch (error) {
            setnextbtnPressed(false);
            props.history.push('/email')
            toast.error("Try again ...");
        }
    }

    React.useEffect(() => {
        validateToken()
    }, [])


    if (loading) {
        return (
            <div className="container">
                <Spinner
                    as="span"
                    animation="grow"
                    size="sm"
                    role="status"
                    aria-hidden="true"
                />
            </div>
        );
    } else {
        return <div className="container">
            <AuthHeader naviagtion={props.history} screenType="Home" />
            <h3 className="textHeadauth">Set a new password</h3>
            <Modal
                show={dshow}
                onHide={() => setdshow(!dshow)}
                backdrop="static"
                keyboard={false}
                size="sm"
                aria-labelledby="contained-modal-title-vcenter"
                centered
            >
                <Modal.Body>
                    <p className="textHead">{ddata[0]}</p>
                    <p className="textNormal">{ddata[1]}</p>
                </Modal.Body>
                <Button variant="light" style={{ color: colorsFinal.brandPrimary }} onClick={() => setdshow(!dshow)}>
                    Okay
                </Button>
            </Modal>
            <div class="floating-form">
                <div class="floating-label" >
                    <input class="floating-input" value={password} onChange={(p) => checkPass(p.target.value)} id="typepass" type="password" placeholder="" />
                    <span class="highlight"></span>
                    <label className="floating-label-input" >Password</label>
                    {pass ? (<FiEye class="i" onClick={() => showPass()} />) : (<FiEyeOff class="i" onClick={() => showPass()} />)}
                </div>
                {helperType === 'pass' ? (
                    <p className="textHelper" style={{ marginTop: '10px', }}>
                        Password strength:{' '}
                        <span className="textHelper" style={{ color: helperColor }}>
                            {helperText}
                        </span>
                    </p>
                ) : null}
            </div>
            <div className="footer">
                {nextbtnPressed ? (
                    <>
                        <Button variant="outline-light" style={{
                            color: '#fff',
                            backgroundColor: '#00A6A4',
                            opacity: 0.4,
                            borderRadius: 10,
                            width: '100%',
                            height: '44px'
                        }}>
                            <Spinner
                                as="span"
                                animation="grow"
                                size="sm"
                                role="status"
                                aria-hidden="true"
                            />
                            Confirm
                        </Button>

                    </>
                ) : (
                    <Button variant="outline-light" style={{
                        color: '#fff',
                        backgroundColor: '#00A6A4',
                        borderRadius: 10,
                        width: '100%',
                        height: '44px',
                        opacity: isValid() ? 1 : 0.4,

                    }}
                        onClick={() => onContinuePressed()}
                    >
                        Confirm
                    </Button>)}
            </div>
        </div>
    }
}
