import 'bootstrap/dist/css/bootstrap.min.css';
import React from "react";
import { isMobileOnly } from 'react-device-detect';
import { BrowserRouter as Router, Switch } from 'react-router-dom';
import { ToastContainer } from "react-toastify";
import 'react-toastify/dist/ReactToastify.css';
import "./App.css";
import minbox from "./assets/SVG/mintbox_logo.svg";
import HomeLayout from "./views/mobileView/route";

class App extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      load: true,
    };
  }

  changeThemeVal() {
    var theme = window.localStorage.getItem("theme")
    if (theme === null || theme === undefined) {
      window.localStorage.setItem("theme", 'light')
      document.documentElement.setAttribute("data-theme", "light")
      document.body.style = 'background: #fff;';
    } else {
      if (theme === 'dark') {
        document.documentElement.setAttribute("data-theme", "dark")
        document.body.style = 'background: #001516;';
      } else {
        document.documentElement.setAttribute("data-theme", "light")
        document.body.style = 'background: #fff;';
      }
    }
  }

  componentDidMount() {
    this.changeThemeVal()
    this.setState({
      load: false,
    });
  }

  render() {
    if (isMobileOnly) {
      return (
        <div className="body">
          <ToastContainer draggable={true} />
          <Router>
            <Switch>
              <HomeLayout />
            </Switch>
          </Router>
        </div>
      );
    } else {
      return (
        <div
          style={{
            display: "flex",
            flexDirection: "column",
            minHeight: "90vh",
            alignItems: "center",
            justifyContent: "center"
          }}
        >
          <ToastContainer />
          <div>
            <img src={minbox} height="100px" width="100%" />
          </div>
          <h1 style={{ textAlign: "center" }}>HEY! WE'RE BUILDING OUR WEBSITE ... </h1>
        </div>
      );
    }
  }
}

export default App;
