import React from 'react';
import { Button, Spinner } from 'react-bootstrap';
import { emailValidator } from '../../../../../libs/validators';
import AdminServices from '../../../../../services/api/AdminServices';
import AuthHeader from '../../../components/molecules/Headers/AuthHeader';
import { toast } from 'react-toastify';

export default function SignupScreen(props) {

    const [name, setName] = React.useState('');
    const [email, setEmail] = React.useState('');
    const [phone, setPhone] = React.useState('');
    const [mainOTP, setmainOTP] = React.useState('');
    const [nextbtnPressed, setnextbtnPressed] = React.useState(false);
    const [loading, setloading] = React.useState(true);
    const [helperType, sethelperType] = React.useState('');
    const [helperColor, sethelperColor] = React.useState('');
    const [helperText, sethelperText] = React.useState('');

    const onName = (n) => {
        setName(n);
        if (n.match(/[^a-zA-Z\s]/g)) {
            sethelperType('name');
            sethelperColor('red');
            sethelperText("Name can't be alphanumeric");
        } else {
            sethelperType('');
            sethelperColor('');
            sethelperText('');
        }
    };

    const onPhone = (p) => {
        console.log(p.length)
        setPhone(p);
        setnextbtnPressed(false);
        setmainOTP('');

        if (p.match(/[^0-9\s]/g)) {
            sethelperType('phone');
            sethelperColor('red');
            sethelperText('Enter valid number');
        } else {
            sethelperType('');
            sethelperColor('');
            sethelperText('');
        }
    };

   
    const validInput = () =>  name
    && emailValidator(email).isValid
    && phone.length === 10
    && helperText === ''

    const onNextBtnPressed = async () => {
        setnextbtnPressed(true);

        try {
            const res = await AdminServices.sendOTP(phone, email).then((res) => res);
            if (res.type) {
                if (res.data.Result === 'success') {
                    props.history.push({
                        pathname: '/otp_screen',
                        state: {
                            name,
                            email,
                            phone,
                            otp: res.data.otpcode
                          } // your data array of objects
                      })
                   
                } else {
                    setnextbtnPressed(false);
                    toast.error("Try again ...");
                }
            } else {
                setnextbtnPressed(false);
                toast.error("Try again ...");
            }
        } catch (error) {
            setnextbtnPressed(false);
            toast.error("Try again ...");
        }
    };

    React.useEffect(() => {
        const { state } = props.location
        console.log(state)
        if (state === null || state === undefined) {
            props.history.push('/email')
        } else {
            setEmail(state.email)
            setloading(false)
            setnextbtnPressed(false);
        }
    },[])


    if (loading) {
        return (
            <div className="container">
                <Spinner
                    as="span"
                    animation="grow"
                    size="sm"
                    role="status"
                    aria-hidden="true"
                />
            </div>
        );
    } else {
        return <div className="container">
            <AuthHeader navigation={props.history} />
            <h3 className="textHeadauth">Hello! Sign up to explore</h3>
            <div className="floating-form">
                <div className="floating-label">
                    <input className="floating-input" disabled value={email} onChange={(p) => setEmail(p.target.value)} type="email" placeholder="" />
                    <span className="highlight"></span>
                    <label className="floating-label-input" >Email</label>
                </div>
                <div className="floating-label">
                    <input className="floating-input" value={name} onChange={(p) => onName(p.target.value)} type="text" placeholder="" />
                    <span className="highlight"></span>
                    <label className="floating-label-input" >First name</label>
                    {helperType === "name" ? <span className="textHelper primary" style={{ color: helperColor }} >{helperText}</span> : null}
                </div>
                <div className="floating-label">
                    <input className="floating-input" maxLength={10} value={phone} onChange={(p) => onPhone(p.target.value)} type="tel" placeholder="" />
                    <span className="highlight"></span>
                    <label className="floating-label-input" >Phone number</label>
                    {helperType === "phone" ? <span className="textHelper primary" style={{ color: helperColor }} >{helperText}</span> : null}
                </div>
            </div>
            <div className="footer">
                {nextbtnPressed ? (
                    <>
                        <Button variant="outline-light" style={{
                            color: '#fff',
                            backgroundColor: '#00A6A4',
                            opacity: 0.4,
                            borderRadius: 10,
                            width: '100%',
                            height: '44px'
                        }}>
                            <Spinner
                                as="span"
                                animation="grow"
                                size="sm"
                                role="status"
                                aria-hidden="true"
                            />
                            Sign up...
                        </Button>

                    </>
                ) : (
                    <Button variant="outline-light" style={{
                        color: '#fff',
                        backgroundColor: '#00A6A4',
                        borderRadius: 10,
                        width: '100%',
                        height: '44px',
                        opacity: validInput() ? 1 : 0.4,
                    }}
                        onClick={() => onNextBtnPressed()}
                    >
                        Sign up
                    </Button>)}
                <div className="footer-content">
                    <p className="textNormal ">Already have an account? <span onClick={() => props.history.goBack()} className="primary">Sign in</span></p>
                    <p className="textHelper place-holder" >By signing up, you agree to the <span className="primary">terms and conditions </span>
                        and <span className="primary">privacy policy</span> of Neam Caps Pvt Ltd</p>
                </div>
            </div>
        </div>
    }
}
