import React from "react";
import { Button, Modal, Spinner } from "react-bootstrap";
import { toast } from 'react-toastify';
import { emailValidator } from "../../../../../libs/validators";
import AdminServices from "../../../../../services/api/AdminServices";
import colorsFinal from "../../../../../theme/colors";
import AuthHeader from "../../../components/molecules/Headers/AuthHeader";

export default function ResetPasswordScreen(props) {

  const [email, setEmail] = React.useState('');
  const [nextbtnPressed, setnextbtnPressed] = React.useState(false);
  const [nextbtnPressed2, setnextbtnPressed2] = React.useState(false);
  const [dshow, setdshow] = React.useState(false);
  const [ddata, setddata] = React.useState([]);
  const [loading, setloading] = React.useState(true);

  React.useEffect(() => {
    const { state } = props.location
    console.log(state)
    if (state === null || state === undefined) {
      props.history.push('/email')
    } else {
      setEmail(state.email)
      setloading(false)
      setnextbtnPressed(false);
    }
  }, [])

  const onNextBtnPressed = async () => {
    const data = {
      email,
      clientId: props.location.state.ClientId
    };
    setnextbtnPressed2(true);
    try {
      const res = await AdminServices.emailExist(email).then((res) => res);
      if (res.type) {
        if (res.data.ClientId !== '') {
          try {
            const res = await AdminServices.forgotPass(data).then((res) => res);
            if (res.msg === 'Success') {
              setddata(["Check your inbox!", "We have sent you an email with a link to reset your password"]);
              setdshow(true);
              setnextbtnPressed2(false);
            }
          } catch (err) {
            toast.error("Try again ...");
          }
        } else {
          setnextbtnPressed(false);
          setnextbtnPressed2(false);
          setddata(["User not registered", "Signup First"]);
          setdshow(true);
        }
      } else {
        setnextbtnPressed(false);
        setnextbtnPressed2(false);
        toast.error("Try again ...");
      }
    } catch (error) {
      setnextbtnPressed(false);
      setnextbtnPressed2(false);
      toast.error("Try again ...");
    }
  };


  if (loading) {
    return (
      <div className="container">
        <Spinner
          as="span"
          animation="grow"
          size="sm"
          role="status"
          aria-hidden="true"
        />
      </div>
    );
  } else {
    return (
      <div className="container">
        <AuthHeader navigation={props.history} />
        <h3 className="textHeadauth">Reset password</h3>
        <h3 className="textNormal place-holder">Enter your registered email to reset password</h3>

        <Modal
          show={dshow}
          onHide={() => setdshow(!dshow)}
          backdrop="static"
          keyboard={false}
          size="sm"
          aria-labelledby="contained-modal-title-vcenter"
          centered
        >
          <Modal.Body>
            <p className="textHead">{ddata[0]}</p>
            <p className="textNormal">{ddata[1]}</p>
          </Modal.Body>
          <Button variant="light" style={{ color: colorsFinal.brandPrimary }} onClick={() => setdshow(!dshow)}>
            Okay
          </Button>
        </Modal>


        <div className="floating-form">
          <div className="floating-label">
            <input value={email} onChange={(p) => setEmail(p.target.value)} className="floating-input" type="email" placeholder="" />
            <span className="highlight"></span>
            <label className="floating-label-input">Email</label>
          </div>
        </div>
        {nextbtnPressed2 ? (
          <center>
            <Spinner
              as="span"
              animation="grow"
              size="sm"
              role="status"
              aria-hidden="true"
              color={colorsFinal.brandPrimary}
            />
          </center>

        ) : null}
        <div className="footer">
          {nextbtnPressed ? (
            <>
              <Button variant="outline-light" style={{
                color: '#fff',
                backgroundColor: '#00A6A4',
                opacity: 0.4,
                borderRadius: 10,
                width: '100%',
                height: '44px'
              }}>
                <Spinner
                  as="span"
                  animation="grow"
                  size="sm"
                  role="status"
                  aria-hidden="true"
                />
                Continue
              </Button>

            </>
          ) : (
            <Button style={{

              opacity: emailValidator(email).isValid ? 1 : 0.4,
            }}
              onClick={
                emailValidator(email).isValid ? (
                  () => {
                    onNextBtnPressed();
                  }
                ) : (
                  () => { }
                )
              } variant="primary" className="btn-footer">
              Continue
            </Button>
          )}
        </div>
      </div>
    );
  }
}
